/* 
    输入秒数

    转换为分秒
*/

// 时间处理
export default class MyDate {
  //   constructor() {}

  /* 
  处理音乐时间的方法
        @ params {
            milliseconds: 秒数 number
        }
    */

  static withMilliseconds(milliseconds: number): string {
    let left: string, right: string;
    //  10分钟以上
    if (milliseconds / 60 >= 10) {
      left = "" + Math.floor(milliseconds / 60);
      const mores: number = milliseconds - Math.floor(milliseconds / 60) * 60;
      right = mores % 60 > 9 ? mores + "" : "0" + mores;
    }
    // 1 分钟以外 10 分钟以内
    else if (milliseconds / 60 > 0 && milliseconds / 60 < 10) {
      left = "0" + Math.floor(milliseconds / 60);
      const mores: number = milliseconds - Math.floor(milliseconds / 60) * 60;
      right = mores % 60 > 9 ? mores + "" : "0" + mores;
    }
    // 59s  -  10s
    else if (milliseconds % 60 >= 10 && milliseconds % 60 < 60) {
      left = "00";
      right = milliseconds % 60 > 9 ? milliseconds + "" : "0" + milliseconds;
    }
    // 0-9s
    else {
      left = "00";
      right = "0" + milliseconds;
    }

    return `${left}:${right}`;
  }
}
